.about-section {
  padding: 5%;
  max-width: var(--width-container);
  margin: 0 auto;
}

.about-section img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.about-section article {
  margin-top: 5%;
}

.about-section h2 {
  color: var(--light-pink);
  font-weight: bold;
  margin-bottom: 2%;
}

.cta-btns-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  height: 300px;
  gap: 30px;
}

.cta-btns-container a {
  text-decoration: none;
  color: var(--dark-orange);
  border: 2px solid var(--dark-orange);
  border-radius: 3px;
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.126);
  padding: 10px 15px;
  transition: all 0.4s;
}

.cta-btns-container a:hover {
  box-shadow: 0px 3px 3px rgba(0, 0, 0, 0.130);
  background-color: var(--dark-orange);
  color: var(--white);
}

@media only screen and (max-width: 400px) {
  .cta-btns-container {
    flex-direction: column;
  }
}
